* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#root {
  background-color: #212529;
}

.App {
  padding-top: 2rem;
  width: 80%;
  margin: 0 auto;
}

.data-container {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid rgba(0, 0, 0, 0.125);
}

.form-container {
  margin-top: 2rem;
}

#card {
  border: 0;
}

.align-top {
  padding-right: 0.2rem;
}

.logo {
  cursor: pointer;
}
